}

.derecha {
  display: grid;
  grid-template-columns: 3.6rem auto;
  column-gap: 2rem;
  width: 50%;
}
.derecha .navegacion {
  grid-column: 2;
}
@media (max-width: 550px) {
  .derecha {
    display: block;
  }
}
@media (max-width: 880px) {
  .derecha {
    display: block;
  }
}

.barra {
  padding: 1rem 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.barra .derecha .contenedor-darkmode {
  width: 3.6rem;
  filter: invert(100%);
