}
.barra .derecha .contenedor-darkmode img {
  margin: 1rem auto;
}
.barra .derecha .contenedor-darkmode:hover {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .barra .derecha .contenedor-darkmode {
    margin: 0 0;
  }
}
@media (max-width: 880px) {
  .barra {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .barra a {
    width: 100%;
  }
  .barra a img {
    margin: 0 auto;
  }
}
.barra .derecha {
  width: 60%;
}
@media (max-width: 550px) {
  .barra .derecha {
    width: 100%;
  }
}
@media (max-width: 880px) {
  .barra .derecha .navegacion {
    padding: 1rem 0;
    padding-bottom: 2rem;
    flex-direction: column;
  }
  .barra .derecha .navegacion a {
    margin-top: 2rem;
  }
}
@media (max-width: 880px) {
  .barra .hover-nav nav {
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .barra .hover-nav a {
    position: relative;
    z-index: 0;
  }
  .barra .hover-nav a:before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    bottom: 0;
    left: 0;
    background-color: #f6c126;
    z-index: -1;
    transform: scale(0);
    transition: transform 0.5s ease-in-out;
  }
  .barra .hover-nav a:hover:before,
.barra .hover-nav a:focus:before {
    transform: scale(1);
  }
}
@media (max-width: 550px) {
  .barra .hover-nav nav {
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .barra .hover-nav a {
    position: relative;
    z-index: 0;
  }
  .barra .hover-nav a:before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    bottom: 0;
    left: 0;
    background-color: #f6c126;
    z-index: -1;
    transform: scale(0);
    transition: transform 0.5s ease-in-out;
  }
  .barra .hover-nav a:hover:before,
.barra .hover-nav a:focus:before {
    transform: scale(1);
  }
}

